<template>
  <div
    :class="[...classStyles]"
    @mouseenter="isHover = true"
    @mouseleave="isHover = false"
  >
    <div
      :class="[
        'accordion__header'
      ]"
      :style="{
        cursor: trigger !== 'header' && 'default'
      }"
      @click="toggleCollapse">
      <slot v-bind="{ isCollapse, isHover, disableExpand, toggleCollapse }" name="header">
        <div class="flex items-center">
          <div
              :class="{ 'el-table__expand-icon el-table__expand-icon--expanded': isCollapse, 'el-table__expand-icon': !isCollapse }"
            >
              <i
                class="el-icon-arrow-right"
              >
            </i>
            </div>
          <div class="flex-grow">
            <span class="block font-bold ml-2">{{ label }}</span>
          </div>
        </div>
      </slot>
    </div>
    <div :style="customStyles.content" class="accordion__content">
      <div class="px-4 py-2">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, ref, toRef, watch } from "vue"
export default defineComponent({
  name: "Accordion",
  props: {
    block: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: [Number, String],
      default: undefined
    },
    label: {
      type: String,
      default: ""
    },
    disableExpand: {
      type: Boolean,
      default: false
    },
    modelValue: {
      type: Boolean,
      default: false
    },
    trigger: {
      type: String,
      default: 'header'
    }
  },
  emits: ["collapse", "update:modelValue"],
  setup(props, { emit }) {
    const maxHeight = toRef(props, "maxHeight")
    const label = toRef(props, "label")
    const isBlock = toRef(props, "block")
    const isCollapse = ref(props?.modelValue ?? false)
    const isHover = ref(false)

    const toggleCollapse = () => {
      if (props.disableExpand || props.trigger === 'content') return null
      isCollapse.value = !isCollapse.value
      if (isCollapse.value) emit("collapse", true)
      emit('update:modelValue', isCollapse.value)
    }

    const isMaxHeightSet = computed(() => maxHeight.value ?? false)

    const classStyles = computed(() => {
      const classes = ["accordion"]
      if (isCollapse.value) classes.push("--collapse")
      if (isBlock.value) classes.push("--block")
      return classes
    })

    const customStyles = computed(() => {
      const styles = {
        content: {}
      }
      if (isMaxHeightSet.value)
        Object.assign(styles.content, {
          maxHeight: maxHeight.value.includes('%') ? `${maxHeight.value}` : `${maxHeight.value}px`
        })
      return styles
    })
    watch(() => props.modelValue, () => {
      isCollapse.value = props.modelValue
    })
    return {
      label,
      isCollapse,
      isHover,
      toggleCollapse,
      classStyles,
      customStyles
    }
  }
})
</script>
<style lang="scss" scoped>
.accordion {
  @apply px-4 py-2 bg-grey-lighten rounded-lg;
  &.--block {
    @apply w-full max-w-full;
  }
  .accordion__content {
    @apply max-h-96 overflow-auto transition-all duration-300;
  }
  .accordion__header {
    @apply py-2 cursor-pointer;
    .accordion__header_arrow {
      @apply flex items-center justify-center px-4 transition-transform duration-300;
    }
  }
  &.--collapse {
    .accordion__header_arrow {
      @apply transform rotate-180;
    }
  }
  &:not(.--collapse) {
    .accordion__content {
      @apply max-h-0 overflow-hidden #{!important};
    }
  }
}

.accordion {
  @apply mb-4;
  width: 100%;
  background-color: white;
  border: solid 1px rgba(0, 0, 0, 0.1);
  &.--collapse {
    .accordion__header_arrow {
      @apply transform rotate-180;
    }
    &.accordion-workflow {
      @apply bg-white hover:bg-white;
    }
  }
}

.accordion__header_arrow {
  &.--hide {
    @apply opacity-0;
  }
}
.accordion {
  &.accordion-workflow {
    @apply bg-white;
    &:hover {
      @apply bg-grey-lighten;
    }
    &--disabled {
      @apply bg-opacity-10 bg-grey-01;
      &:hover {
        @apply bg-opacity-10 bg-grey-01;
      }
    }
  }
}
</style>
